.theme-dark .board-card{background-color:#535657}.theme-dark .board-card:hover{background-color:#585b5c}.theme-dark .board-card:hover .board-card__columns__column{background:#6a6d6e}.theme-dark .board-card__columns__column{background:#5a5e5f}.theme-dark .board-card__content__title{color:#f0f0f0}.theme-dark .board-card__content__menu svg{color:#f0f0f0;fill:#f0f0f0}.theme-dark .board-card__content__menu svg:hover{background:#5d6061}

